.modal-form {
    color: $cl-dark;
    font-size: $fz-small;
    background: rgba($cl-white, .75);
    border: 2px solid rgba($cl-white, .75);
    border-radius: 1rem;
    padding: 2rem;
    margin: 7.5rem;

    .modal-group {
        display: grid;
        grid-template-rows: 1fr;
        grid-template-columns: 2fr 6fr 2fr;
        margin-bottom: 2rem;

        .modal-header {
            justify-self: start;
            align-self: center;
            margin-bottom: 3rem;
        }

        .modal-close {
            justify-self: end;
            align-self: center;
            margin-bottom: 3rem;
            cursor: pointer;
        }

        .modal-error {
            font-size: $fz-mini;
            color: $cl-red;
            padding-left: 1rem;
            justify-self: start;
            align-self: center;
        }

        .modal-button {
            display: grid;
            grid-template-rows: 1fr;
            grid-template-columns: repeat(10, 1fr);
            grid-column-gap: 2rem;

            .button-dismiss {
                grid-row: 1 / 2;
                grid-column: 9 / 10;
            }

            .button-submit {
                grid-row: 1 / 2;
                grid-column: 10 / 11;
            }
        }

        .modal-gecko {

            display: grid;
            grid-template-rows: 1fr;
            grid-template-columns: 5fr 1fr;
            grid-column-gap: 2rem;

            .modal-fetch {
                width: 100%;
                justify-self: end;
                align-self: center;
                border-radius: 1rem;
            }
        }
    }
}